// directives/bounceIn.js
export default {
  mounted(el) {
    // Start with hidden and off-screen

    const observer = new IntersectionObserver(
      ([entry], observer) => {
        if (entry.isIntersecting) {
          el.classList.add('animationClass');
          observer.unobserve(el);
        }
      },
      {
        threshold: 0.7,
      }
    );

    observer.observe(el);
  }
};
